:root {
  --rain-bg-color: #2d3436;
  --rain-grad-first: #182C61;
  --rain-grad-last: #f5f6fa;
  --text-cursor-color: #dff9fb;
  --highlight-color: #7f8fa6;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: white;
  width: initial;
}

::-webkit-scrollbar {
  background-color: transparent;
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: var(--text-cursor-color);
}

::selection {
  background-color: var(--highlight-color);
}

html, body {
  height: 100vh;
}

.rain-canvas {
  background-color: var(--rain-bg-color);
  background-image: linear-gradient(to bottom, var(--rain-grad-first), var(--rain-grad-first), var(--rain-grad-last));
}

#root, #root > div {
  height: 100vh;
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -999;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text-cursor {
  position: relative;
  width: fit-content;
}

.text-cursor:after {
  position: absolute;
  top: 0;
  left: 100%;
  height: 100%;
  content: ' ';
  padding-left: 1px;
  border-right: 2px solid var(--text-cursor-color);
  animation: cursor-blink 1s linear infinite;
}

.highlighted {
  color: white;
  overflow: hidden;
  width: initial;
}

.highlighted:before {
  content: ' ';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--highlight-color);
  z-index: -1;
  animation: block 0.75s linear;
}

@keyframes block {
  0% {
    width: 1px;
  }
  100% {
    width: 100%;
  }
}

@keyframes cursor-blink {
  0%, 100% {
    border-right-color: var(--text-cursor-color);
  }

  50% {
    border-right-color: transparent;
  }
}

.menu {
  text-transform: capitalize;
  cursor: pointer;
  border-bottom: none;
  padding: 8px 16px;
}

.menu-active {
  border-bottom: 2px solid white;
}

.box-title {
  font-size: 25px;
  margin-bottom: 2.5%;
  margin-top: 1.5%;
  font-weight: 400;
}